.сompanyNav {
  display: flex;
  height: 70px;
  width: calc(100% - 150px);
}
.companyNavBlock {
  display: flex;
  background-color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 70px;
  height: 70px;
  border: 2px solid #c4c4c4;
  cursor: pointer;
  border-right: none;
}
.companyNavLink {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.companyNavBlock:last-of-type {
  border-right: 2px solid #c4c4c4;
}
.allText {
  font-weight: bold;
  font-size: 14;
  text-transform: uppercase;
}
.сompanyNavText {
  margin-top: 4px;
  font-size: 12px;
  font-weight: bold;
  line-height: 14px;
}
@media screen and (max-width: 760px) {
  .сompanyNav {
    overflow: hidden;
    align-items: center;
    white-space: nowrap;
    height: 50px;
    width: 100%;
  }
  .activeBtnScroll {
    cursor: grabbing;
    cursor: -webkit-grabbing;
  }
  .companyNavBlock {
    min-width: 75px;
    height: 30px;
    background: #ffffff;
    border: 1px solid #e5e5e5;
    box-sizing: border-box;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    margin-right: 12px;
  }
  .activeBtn {
    background: #e32a6c;
    transition: 0.3s ease all;
    box-shadow: 0px 4px 2px rgba(103, 103, 103, 0.25);
  }
  .activeBtnText {
    transition: 0.3s ease all;
    color: #ffffff;
  }
  .сompanyNavText,
  .allText {
    text-transform: none;
    font-family: Roboto, Geneva, Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: normal !important;
    font-size: 12px;
    letter-spacing: 0.05em;
  }
  .companyNavLink img {
    display: none;
  }
}
