  .SmallCompanyBlock {
    height: 320px;
    width: calc(50% - 10px);
    display: flex;
    justify-content: space-around;
  }
  .imgContainer {
    height: 280px;
    border-radius: 5px;
  }
  .companyImg {
    height: 280px;
    border-radius: 5px;
  }
  .gradientBlock {
    padding: 5px 10px;
    height: 53px;
    position: relative;
    top: -57px;
    border-radius: 5px;
    display: block;
    background: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0.1596) 0%,
      rgba(0, 0, 0, 0.3) 13.54%
    );
  }
  .gradientBlockName {
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
    letter-spacing: 0.05em;
    color: #ffffff;
  }
  .gradientBlockType {
    color: #f60185;
    font-weight: bold;
  }
  .description {
    display: none;
  }
  .descriptionMobile {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: center;
    padding: 0 5px;
  }
  .endTime {
    font-size: 13px;
    line-height: 15px;
    padding-bottom: 3px;
    font-weight: normal;
  }
  .distance {
    font-weight: 500;
    font-size: 11px;
    line-height: 13px;
    letter-spacing: 0.05em;
  }
  .distanceFirst,
  .nameOfCompany {
    display: none;
  }
}
