.burger {
  display: none;
}
@media screen and (max-width: 760px) {
  .burger {
    display: inline-block;
    position: absolute;
    right: 20px;
    top: 14px;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 3;
    transition: 0.4s ease all;
  }
  .burger > span,
  .burger > span::before,
  .burger > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    transition: 0.4s ease all;
    background-color: #616161;
  }
  .burger > span::before {
    transition: 0.4s ease all;
    content: "";
    top: 10px;
  }
  .burger > span::after {
    transition: 0.4s ease all;
    content: "";
    top: 20px;
  }
}

.activeburger {
  display: inline-block;
  position: absolute;
  right: 20px;
  top: 14px;
  width: 26px;
  height: 26px;
  cursor: pointer;
  z-index: 3;
  transition: 0.3s ease all;
}
.activeburger > span,
.activeburger > span::before,
.activeburger > span::after {
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #616161;
}
.activeburger > span::before {
  content: "";
  transition: 0.1s ease all;
  opacity: 0;
}
.activeburger > span {
  transform: rotate(45deg);
  top: 10px;
  transition: 0.3s ease all;
}
.activeburger > span::after {
  content: "";
  transform: rotate(90deg);
  transition: 0.3s ease all;
  left: 0;
  top: 0;
}
